@use "../base/globales" as g;
@use "../base/variables" as v;
@use "../base/mixins" as m;

.nuestros-testimoniales {
  padding: 2rem;
  background-color: v.$blanco;
  @include m.tablet {
    padding: 10rem 2rem;
  }
}
.testimonial {
  background-color: v.$cafeClaro;
  padding: 2rem 4rem;
  max-width: 50rem;
  margin: 0 auto;
  border-radius: 1rem;
  .autor {
    text-align: right;
    font-size: 2.4rem;
    color: v.$primario;
    font-family: v.$fuenteCursiva;
  }
}
