@use "../base/variables" as v;
@use "../base/mixins" as m;

/*****Header******/

.header {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5rem 0;
  position: relative;
  @include m.tablet {
    min-height: 70rem;
  }

  &::before {
    content: "";
    background-color: v.$negro;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.5;
  }
  .contenido-header {
    position: relative;
  }
}

.texto-header {
  color: v.$blanco;
  text-align: center;
  text-transform: uppercase;
  margin-top: 5rem;
  @include m.tablet {
    margin-top: 10rem;
  }
}
/*****Header de cada pagina******/
.header-inicio {
  background-image: url(../img/header_inicio.jpg);
}
.header-nosotros {
  background-image: url(../img/header_nosotros.jpg);
}
.header-proceso {
  background-image: url(../img/header_proceso.jpg);
}
.header-menu {
  background-image: url(../img/header_menu.jpg);
}
.header-galeria {
